export default {
	data() {
		return {
			imageArr: []
		}
	},
	mounted() {
		this.imageArr = uni.getStorageSync("chooseImg")
	},
	methods: {
		updateImage() {
			uni.chooseImage({
				count: 9,
				success: res => {
					this.imageArr = res.tempFilePaths
					
					uni.setStorageSync("chooseImg",this.imageArr)
					

					console.log(this.imageArr)

				}
			})
		}
	}
}
